body,
html {
    color: $font-color;
    font-family: $font-stack-lato;
    font-size: 62.5%;
    height: 100%;
}

.navbar-default {
    background-color: $white;
    margin-bottom: 0;
    min-height: 2rem;
    padding: 1rem 0;

    a {
        color: $grey;
    }

    a.active {
        color: $transit-blue;
    }
}

h1 {
    @include font-size(16, 10);
    @include screen ('desktop') {
        @include font-size(30, 10);
    }
}

h2 {
    @include font-size(12, 10);
    @include screen ('desktop') {
        @include font-size(24, 10);
    }
}

p {
    @include font-size(10, 10);
}

a {
    @include font-size(12, 10);
}

.btn {
    @include font-size(13, 10);
}

hr {
    color: $light-grey;
    background-color: $light-grey;
}

small {
    @include font-size(8, 10);
    @include screen ('desktop') {
        @include font-size(12, 10);
    }
}

